@charset "UTF-8";

// 本頁專用變數(覆蓋)
// ------------------------------------------------------------
$isPage:                 true;

$headings-line-height:   1.2;
$link-transition:        '.35s';

$font-size-h1:           46px;
$font-size-h2:           42px;
$font-size-h3:           27px;

// import
// ------------------------------------------------------------
@import "_client/variables"; // 前台用變數(置頂)

@import "core/function";
@import "core/variables";
@import "core/mixins";

// 由此 output css
@import "vendor/normalize";  // 重置樣式
@import "vendor/print";      // 列印樣式

@import "_client/base/basic";
@import "_client/base/link";
@import "_client/base/type";
@import "_client/base/table";
@import "_client/base/form";

@import "_client/layout";
@import "_client/modules/nav";
@import "_client/modules/btn";
@import "_client/modules/btn-group";
@import "_client/modules/label";
@import "_client/modules/field";
@import "_client/modules/figure";
@import "_client/modules/loader";

@import "_client/util"; // 建議放置在最後一個


// main
// ------------------------------------------------------------

#main {
    text-align: left;
}

// == common

// breadcrumb
.breadcrumb {
    @extend %list-unstyled;
    margin: 15px 0 0 0;
    float: right;
    font-size: em(12px);
    color: #466c75;

    a {
        color: #8ca8af;
    }

    li {
        display: inline-block;

        &:not(:last-child) {

            @include el(after, false, '/') {
                margin: 0 4px;
                color: #8ca8af;
            }
        }
    }
}

// title
h3.title {
    margin: 0 0 $spacer*2 0;
    padding-bottom: $spacer;
    font-size: em(32px);
    color: #000;
    border-bottom: 1px solid #aebcbf;
    @include clearfix;

    span {
        letter-spacing: 0;
        font-weight: 300;
        font-style: italic;
        font-size: em(24px, 32px);
        color: #6cb1c4;
    }
}

// thumbnail
.thumbnail {
    padding: $spacer/2;
    border: 1px solid #ddd;
}
img.thumbnail {
    max-width: 100%;
}

// 不換行
.text-nowarp {
    white-space: nowrap;
}

// 無圖時
.none {
    background-color: lighten($muted, 8);
    border: 1px solid lighten($muted, 4);
    color: $muted;
    position: relative;

    i {
        @extend .font-xxl;
        @include center;
    }
}

// section
.section {
    margin-bottom: $spacer*2;
    padding-left: $spacer;

    h4.title {
        min-height: 54px;
        margin-top: 0;
        margin-left: -$spacer;
        padding: 10px 0 0 60px;
        position: relative;
        vertical-align: middle;
        font-size: em(24px);
        font-weight: bold;
        
        em {
            display: block;
            @include circle(54px, center);
            position: absolute;
            left: 0;
            top: 0;
            line-height: 46px;
            font-size: em(32px, 24px);
            font-style: normal;
            font-weight: normal;
            color: #fff;
            border: 4px solid #fff;
            box-shadow: 0 0 1px #999;
        }
    }
    &:nth-of-type(n+1) h4.title {
        color: #a8a566;
        em {
            background: #deda87;
        }
    }
    &:nth-of-type(n+2) h4.title {
        color: #c26f4c;
        em {
            background: #db997d;
        }
    }
    &:nth-of-type(n+3) h4.title {
        color: #785d56;
        em {
            background: #99827d;
        }
    }
    &:nth-of-type(n+4) h4.title {
        color: #92946c;
        em {
            background: #c1c4a5;
        }
    }
}

// == service
.Service {
    h4 {
        margin-top: $spacer;
    }
}

// == Contact Us
.ContactUs {
    .map {
        width: 100%;
        height: 300px;
    }

    h4.title {
        margin-top: 0;
        color: $brand-secondary;
        font-weight: bold;

        span {
            margin-left: 8px;
            font-weight: 300;
            color: desaturate($brand-secondary, 20);
            text-transform: uppercase;
        }
    }
}

// == Public
.gallery {

    li {
        margin: 0 4px 0 8px;
    }

    a {
        display: block;
        position: relative;
        text-align: center;

        &::before, [class*="fa-"] {
            position: absolute;
            opacity: 0;
            transition: opacity 0.75s;
        }

        &::before {
            content: " ";
            display: block;
            width: 165px;
            height: 232px;
            left: 50%;
            top: 9px;
            transform: translateX(-50%);
            background: rgba(#000,0.3);
            z-index: 1;
        }

        [class*="fa-"] {
            left: 50%;
            top: 115px;
            font-size: em(24px);
            color: #fff;
            text-shadow: 0 0 5px #000;
            transform: translate(-50%, -50%);
            z-index: 2;
        }
        
        .thumbnail {
            width: 183px;
            height: 250px;
            margin: 0 auto;
        }

        figcaption {
            margin-top: $spacer*0.5;
            font-size: em(15px);
        }

        &:hover, &:active {
            &::before, [class*="fa-"] {
                opacity: 1;
            }
        }
    }
}

// == Team
.Team {
    table {
        border-spacing: 1px;
        border-collapse: separate;
    }
}

// == editor
.editor {
    @extend .mt-32;
    word-wrap: normal;
    word-break: break-all;

    img {
        max-width: 100% !important;
    }

    ol, ul {
        @extend .ml-24;
    }
}